* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

.Header {
  position: relative;

  .logo {
    top: 2vw;
    left: 5vw;
    z-index: 2;
    color: white;
    cursor: pointer;
    font-size: 1.5vw;
    position: absolute;
    font-family: 'Montserrat', sans-serif;

    @media (max-width:767px) {
      top: 20px;
      left: 30px;
      font-size: 18px;
    }
  }

  .img {
    max-width: 100%;
    min-width: 100%;
  }
}

.Contacts {
  margin-top: -1vw;
  padding: 5vw 15vw;
  padding-bottom: 10vw;
  background-color: rgb(255, 182, 114);

  @media (max-width:767px) {
    padding: 5vw;
    padding-bottom: 20vw;
  }

  .forms {
    gap: 1vw;
    display: flex;
    flex-direction: column;

    @media (max-width:767px) {
      gap: 2vw;
    }

    label {
      color: white;
      cursor: pointer;
      margin-top: 1vw;
      font-size: 1.3vw;
      font-weight: 500;
      font-family: 'Montserrat', sans-serif;

      @media (max-width:767px) {
        font-size: 15px;
        margin-top: 10px;
      }
    }

    .input {
      padding: 1vw;
      outline: none;
      font-size: 1.1vw;
      font-weight: 500;
      border-radius: 0.5vw;
      -webkit-border-radius: 0.5vw;
      -moz-border-radius: 0.5vw;
      -ms-border-radius: 0.5vw;
      -o-border-radius: 0.5vw;
      background-color: white;
      font-family: 'Montserrat', sans-serif;
      border: solid 1px rgb(179, 179, 179);

      &:focus {
        border-color: green;
      }

      @media (max-width:767px) {
        padding: 10px;
        font-size: 15px;
        border-radius: 5px;
        -webkit-border-radius: 5px;
        -moz-border-radius: 5px;
        -ms-border-radius: 5px;
        -o-border-radius: 5px;
      }
    }

    .invalid {
      border-color: red;
    }

    .button {
      border: none;
      padding: 1vw;
      color: white;
      cursor: pointer;
      font-size: 1.3vw;
      font-weight: 500;
      margin-top: 1.5vw;
      border-radius: 0.5vw;
      -webkit-border-radius: 0.5vw;
      -moz-border-radius: 0.5vw;
      -ms-border-radius: 0.5vw;
      -o-border-radius: 0.5vw;
      background-color: dodgerblue;
      font-family: 'Montserrat', sans-serif;

      &:hover {
        opacity: 0.8;
      }

      &:active {
        opacity: 1;
      }

      @media (max-width:767px) {
        padding: 13px;
        font-size: 17px;
        margin-top: 15px;
        border-radius: 5px;
        -webkit-border-radius: 5px;
        -moz-border-radius: 5px;
        -ms-border-radius: 5px;
        -o-border-radius: 5px;
      }
    }
  }
}

.Modal {
  top: 0;
  left: 0;
  z-index: 3;
  width: 100%;
  height: 100%;
  display: flex;
  position: fixed;
  align-items: center;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0.463);

  .child {
    gap: 2vw;
    width: 50%;
    display: flex;
    padding: 4vw 2vw;
    align-items: center;
    border-radius: 0.8vw;
    -webkit-border-radius: 0.8vw;
    -moz-border-radius: 0.8vw;
    -ms-border-radius: 0.8vw;
    -o-border-radius: 0.8vw;
    flex-direction: column;
    background-color: white;

    @media (max-width:767px) {
      gap: 25px;
      width: 80%;
      padding: 20px;
      border-radius: 8px;
      -webkit-border-radius: 8px;
      -moz-border-radius: 8px;
      -ms-border-radius: 8px;
      -o-border-radius: 8px;
    }

    .title {
      font-size: 2vw;
      text-align: center;
      font-family: 'Montserrat', sans-serif;

      @media (max-width:767px) {
        font-size: 22px;
      }
    }

    .btn {
      border: none;
      color: white;
      cursor: pointer;
      font-size: 1.5vw;
      padding: 1vw 5vw;
      font-weight: 600;
      border-radius: 0.8vw;
      -webkit-border-radius: 0.8vw;
      -moz-border-radius: 0.8vw;
      -ms-border-radius: 0.8vw;
      -o-border-radius: 0.8vw;
      background-color: rgb(61, 203, 0);
      font-family: 'Montserrat', sans-serif;

      &:hover {
        opacity: 0.8;
      }

      &:active {
        opacity: 1;
      }

      @media (max-width:767px) {
        font-size: 22px;
        padding: 10px 30px;
        border-radius: 8px;
        -webkit-border-radius: 8px;
        -moz-border-radius: 8px;
        -ms-border-radius: 8px;
        -o-border-radius: 8px;
      }
    }
  }
}